import {message} from 'antd';
import {
  MDBBtn,
  MDBCol,
  MDBContainer,
  MDBIcon,
  MDBInput,
  MDBModal,
  MDBModalBody,
  MDBModalHeader,
  MDBRow,
} from 'mdbreact';
import {inject, observer} from 'mobx-react';


import {Multiselect} from 'multiselect-react-dropdown';
import imageCompression from 'browser-image-compression';
import React, {Component, Fragment} from 'react';
import { Hidden } from '@material-ui/core';
import { number } from 'joi';
class AddArtwork extends Component {
  constructor(props) {
    super(props);

    this.onFileChange = this.onFileChange.bind(this);
    this.onSubmit = this.onSubmit.bind(this);
    this.onImgLoad = this.onImgLoad.bind(this);
    this.state = {
      modal1: false,
      profileImg: '',
      artID: '',
      selectedFile: null,
      imgRaw:null,
      height:null,
      width:null,
      selectedSize:null,
      dpi:null,
    };
  }



  async onFileChange(event) {
  
    var imageFile = event.target.files[0];

  this.setState({imgRaw : URL.createObjectURL(event.target.files[0])})
    
    var options = {
      maxSizeMB: 100,
      maxWidthOrHeight: 10000,
      useWebWorker: false,
    };
    let image = await imageCompression(imageFile, options)
      .then(function (compressedFile) {
        return compressedFile;
      })
      .catch(function (error) {
        console.log(error.message);
      });

 

    this.setState({selectedFile: image});
    
  }

  onImgLoad({target:img}) {
  

    let calcPixels = img.offsetWidth+img.offsetHeight;
    
    this.setState({height:img.offsetHeight,width:img.offsetWidth,dpi:Number(calcPixels/this.state.selectedSize).toFixed(2)});

}

  onSubmit(e) {
    let {
      startingStore: {artwork,upload,listOfArtworks},
    } = this.props;
    let getArtNames = listOfArtworks.filter(data=> data.artName === artwork.artName).length
    let mydata = JSON.parse(sessionStorage.getItem('userData'))
    e.preventDefault();
    if (getArtNames === 0){
    const data = new FormData();
 
    data.append('artworkImg',this.state.selectedFile);
    artwork.setProperty('artistName', `${mydata.accFname} ${mydata.accLname}`)
    artwork.setProperty('artDPI', this.state.dpi)
    data.append('type', 'artwork');
    upload(data);
    const success = () => {
      message
        .loading('Submitting Artwork..', 1.3)
        .then(() => message.success('Artwork submitted', 1));
    };

    setTimeout(() => {
      success();
    }, 1500);
  }else{
    const success = () => {
      message
        .loading('Checking Availability', 1.3)
        .then(() => message.success('Art name already taken', 1));
    };

    setTimeout(() => {
      success();
    }, 1500);
  }
  }

  toggle = (nr) => () => {
    let modalNumber = 'modal' + nr;
    this.setState({
      [modalNumber]: !this.state[modalNumber],
    });
  };

  submitHandler = (event) => {
    event.preventDefault();
    event.target.className += ' was-validated';
  };

  render() {
  
   
    function getHash(input){
      var hash = 0, len = input.length;
      for (var i = 0; i < len; i++) {
        hash  = ((hash << 5) - hash) + input.charCodeAt(i);
        hash |= 0; // to 32bit integer
      }
    
            
      return hash;
    }


  
    let {
      startingStore: {artwork, listOfArtists, listOfCategories, listOfStyles,listOfPrintSize},
    } = this.props;
    let mydata = JSON.parse(sessionStorage.getItem('userData'))
artwork.setProperty('accID',mydata.accID)
artwork.setProperty('artworkStatus','Pending')
    function selectCategory(list, listitem) {
      let artThemeSelected = list.map((cat) => cat.catType);
      artwork.setProperty('artTheme', artThemeSelected);
    }
    function selectStyle(list, listitem) {
      let artStyleSelected = list.map((cat) => cat.styleType);
      artwork.setProperty('artStyle', artStyleSelected);
    }
    let selectArtSize =(list, listitem)=> {
      let artSizeSelected = list.map((size) => size.printSize);
      artwork.setProperty('artSize', artSizeSelected);


        if (artSizeSelected == 'AP - portrait, 1.5x2'){
          this.setState({selectedSize:42})
    
        }
        else if(artSizeSelected == "AP - portrait, 3x4"){
          this.setState({selectedSize:84})
        }else if(artSizeSelected == "B - square, 2x2"){
          this.setState({selectedSize:48})
        }else if(artSizeSelected == "B - square, 4x4"){
          this.setState({selectedSize:96}) 
        }
          
          else if(artSizeSelected == "CP - portrait, 2x3"){
            this.setState({selectedSize:60})
  
        }else if(artSizeSelected == "CP - portrait, 4x6"){
          this.setState({selectedSize:120})
        }else if(artSizeSelected == "DL - landscape, 2x1.5"){
          this.setState({selectedSize:42})
        }else if(artSizeSelected == "DL - landscape, 4x3"){
          this.setState({selectedSize:84})
        }else if(artSizeSelected == "EL - landscape, 3x2"){
          this.setState({selectedSize:60})
        }else if(artSizeSelected == "EL - landscape, 6x4"){
          this.setState({selectedSize:120})
        }
        else {
            this.setState({selectedSize:0})
            console.log('false')
        }
     
     
    }
 
    return (
      <MDBContainer>
        <MDBBtn onClick={this.toggle(1)} color='transparent'>
          <MDBIcon icon='plus-circle' size='2x' className='addartistbtn' />
          Add Artwork
        </MDBBtn>
        <MDBModal isOpen={this.state.modal1} toggle={this.toggle(1)} centered>
          <MDBModalHeader toggle={this.toggle(1)} style={{backgroundColor:'#262626'}}><span style={{color:'white'}}> Register Artwork</span></MDBModalHeader>
          <MDBModalBody className='adminmodalbody'>
            <form onSubmit={this.onSubmit} className='formbtn'>
              <MDBRow>
                <MDBCol>
                  <MDBInput
                    label='Title'
                    type='text'
                    style={{width:'95%'}}
                    onChange={(artName) =>{
                    
                      artwork.setProperty('artName', artName.target.value)
                      artwork.setProperty('artworkID',`${getHash(artName.target.value.slice(0,3))}-${Math.floor(1000 + Math.random() * 9000)}`
                      )
                    }}
                    required
                  >
                    <div className='invalid-feedback'>
                      Please provide a valid email.
                    </div>
                  </MDBInput>
                  <MDBInput
                    label='Artist'
                    type='text'
                    disabled
                    style={{width:'95%'}}
                    valueDefault={`${mydata.accFname} ${mydata.accLname}`}
                  
                  >
                    <div className='invalid-feedback'>
                      Please provide a valid email.
                    </div>
                  </MDBInput>
                  <MDBInput
                    label='Year'
                    type='number'
                    style={{width:'95%'}}
                    onChange={(artworkDateCreated) =>
                      artwork.setProperty(
                        'artworkDateCreated',
                        artworkDateCreated.target.value
                      )
                    }
                    required
                  >
                    <div className='invalid-feedback'>
                      Please provide a First Name.
                    </div>
                  </MDBInput>
                  <MDBInput
                    label='Description'
                    type='text'
                    style={{width:'95%'}}
                    onChange={(artDescription) =>
                      artwork.setProperty(
                        'artDescription',
                        artDescription.target.value
                      )
                    }
                    required
                  >
                    <div className='invalid-feedback'>
                      Please provide a valid password.
                    </div>
                  </MDBInput>
            
              
                  <div  style={{width:'95%'}}>
                  <Multiselect
                 
                    className='multsel'
                    options={listOfCategories}
                    placeholder='Select Theme'
                    showCheckbox={true}
                    displayValue='catType'
                    onSelect={selectCategory}
                    required
                  />
                  </div>
                  
                  <div  style={{width:'95%'}}>
                  <Multiselect
                  style={{width:'95%'}}
                    className='multsel'
                    options={listOfStyles}
                    placeholder='Select Style'
                    showCheckbox={true}
                    onSelect={selectStyle}
                    displayValue='styleType'
                    required
                  />
                   </div>
                </MDBCol>
                <MDBCol>
              
                     <div  style={{width:'95%'}}>
                  <Multiselect
                 
                    className='multsel'
                    options={listOfPrintSize}
                    placeholder='Printing Size'
                    showCheckbox={true}
                    displayValue='printSize'
                    onSelect={selectArtSize}
                    required
                  />
                  </div>
                  <MDBInput
                  style={{width:'95%'}}
                    label='Price'
                    type='number'
                    onChange={(artPrice) =>
                      artwork.setProperty('artPrice', artPrice.target.value)
                    }
                    required
                  >
                    <div className='invalid-feedback'>
                      Please provide a Address.
                    </div>
                  </MDBInput>
                  <select
                  style={{width:'95%'}}
                  required
                    className='usertype'
                    onChange={(artType) =>
                      artwork.setProperty('artType', artType.target.value)
                    }
                  >
                    <option> Art Type </option>
                    <option value='Original'> Original </option>
                    <option value='Secondary'> Secondary </option>
                  </select>
                 
                  <MDBInput
                  style={{width:'95%'}}
                    label='Quantity'
                    type='number'
                    required
                  >
                    <div className='invalid-feedback'>
                      Please provide Number of Quantity.
                    </div>
                  </MDBInput>
                  <div className='uploadreq clearfix'>
                    <input
                    style={{width:'95%'}}
                    required
                      type='file'
                      name='file'
                      disabled={this.state.selectedSize === null  ? true:false}
                      onChange={this.onFileChange}
                      style={{overflow: 'hidden',textOverflow:' ellipsis'}}
                      
                    />
                    <div  style={{height:'0',overflow:'scroll',width:'0',}}>
                    <img onLoad={this.onImgLoad} src={this.state.imgRaw} alt='' visiblity='hide' />
                    </div>
                    {(this.state.height == null)?(
                      <span></span>
                    ):(
                      <Fragment>
                     
              
                     { (this.state.dpi >= 72 )?(
                        <p style={{color:'green'}}>DPI: {this.state.dpi} <MDBIcon far icon="check-circle" style={{color:'green'}}/>  </p>
                      ):(
                        <p style={{color:'red'}}>DPI: {this.state.dpi} - low dpi</p>
                      )
                      }
                      </Fragment>
                    )}
                  
                    <p className='req'>
                      Please upload a high resolution photo with 72dpi and above.
                     
                     
                    </p>
                  </div>
                  <MDBBtn className='submitreg clearfix' type='submit' disabled={this.state.dpi <= 72?true:false}>
                    SUBMIT
                  </MDBBtn>
                </MDBCol>
              </MDBRow>
            </form>
          </MDBModalBody>
        </MDBModal>
      </MDBContainer>
    );
  }
}

export default inject('startingStore')(observer(AddArtwork));
