

import { message} from 'antd';
import {
  MDBBtn, MDBDataTable, MDBModal,
  MDBModalBody,
  MDBModalHeader,
  MDBTable,
  MDBTableBody,
  MDBTableHead,
  MDBModalFooter,
  MDBContainer
} from 'mdbreact';
import { inject, observer } from 'mobx-react';
import moment from 'moment';
import React, { Component, Fragment } from 'react';
import DownloadImage from '../sections/DownloadImage';

 class PendingOrder extends Component {
  state = {
    modal: false,
    items:[],
    modalConfApprove:false,
    modalConfReject:false
   
  };

  render() {
    let { startingStore: {listOfOrders,listOfUsers, editOrder,notif,addNotif,order}} = this.props;
    let userData = JSON.parse(sessionStorage.getItem('userData')) 
    function getHash(input){
      var hash = 0, len = input.length;
      for (var i = 0; i < len; i++) {
        hash  = ((hash << 5) - hash) + input.charCodeAt(i);
        hash |= 0; // to 32bit integer
      }
    
            
      return hash;
    }
   
    
    function createData(orderDB,items,id, orderBy, date, paymentStat,action) {
      return { orderDB,items,id, orderBy, date, paymentStat,action };
    }

let Corder = listOfOrders.filter((Delivery) => {
            if (Delivery.orderStatus === 'Pending' ) {
              return Delivery; }
          }).map(orders =>{
            return(createData(
           orders,orders.orderItems,orders.orderID,listOfUsers.filter(usr=> usr._id === orders.accID).map(user =>( `${user.accFname} ${user.accLname}`
              )),orders.orderDate,orders.paymentStatus
            ))
          })


        let  approve = (itm) => {
       order.setProperty('_id',itm._id)
       order.setProperty('accID',itm.accID)

          this.setState({
            modalConfApprove: true
            
            });

 
          };

          let confirmApprove =()=>{

            editOrder(order._id, 'PendingPrint', order.accID)
            const success = () => {
              message
                .loading('', 1)
                .then(() => message.success('Order Approved', 3));
            };
  
  
            notif.setProperty('notifID',`${getHash(userData.accFname.slice(0,3))}-${Math.floor(1000 + Math.random() * 9000)}`)
            notif.setProperty('notifSender','admin-001')
            notif.setProperty('notifRecipient','print-001')
            notif.setProperty('notifSubject','Printing')
            notif.setProperty('notifMsg','New item to print')
            notif.setProperty('notifDT',moment().format('MMM/DD/YY,h:mm:ssa'))
            notif.setProperty('notifStatus','unread')
  addNotif()
  
            setTimeout(() =>{
              success()
            },1000)
            setTimeout(()=>{
              window.location.reload(false);
            },1500)

          }
          




          let reject =(itm)=>{

            order.setProperty('_id',itm._id)
            order.setProperty('accID',itm.accID)
     
               this.setState({
                 modalConfReject: true
                 
                 });
     
          }


          let confirmReject = ()=>{

            editOrder(order._id, 'Rejected', order.accID)

            const success = () => {
              message
                .loading('', 1)
                .then(() => message.success('Order Rejected', 3));
            };


            notif.setProperty('notifID',`${getHash(userData.accFname.slice(0,3))}-${Math.floor(1000 + Math.random() * 9000)}`)
            notif.setProperty('notifSender','admin-001')
            notif.setProperty('notifRecipient',userData.accID)
            notif.setProperty('notifSubject','Order')
            notif.setProperty('notifMsg','Order Rejected')
            notif.setProperty('notifDT',moment().format('MMM/DD/YY,h:mm:ssa'))
            notif.setProperty('notifStatus','unread')
          addNotif()

            setTimeout(() =>{
              success()
            },1000)
            setTimeout(()=>{
              window.location.reload(false);
            },1500)


          }
    
          let  toggle = (itm) => {


            order.setProperty('orderItems',itm.orderItems)
            order.setProperty('orderID',itm.orderID)
            order.setProperty('modeOfPayment',itm.modeOfPayment)
            order.setProperty('orderDate',itm.orderDate)
            order.setProperty('orderStatus',itm.orderStatus)
            order.setProperty('paymentStatus',itm.paymentStatus)
            order.setProperty('totalAmount',itm.totalAmount)
            order.setProperty('partnerEarnings',itm.partnerEarnings)
            order.setProperty('accID',itm.accID)
         
            this.setState({
            
              items:order.orderItems
            });
        setTimeout(()=>{
          this.setState({
            modal: !this.state.modal,
          
          });
        },100)
         
          
          };
          let close =()=>{
            this.setState({
              modal: false,
              modalConfApprove:false,
              modalConfReject:false
            
            });
          }

const PendingOrderTable = () => {
  const data = {
    columns: [
      {
        label: 'ID',
        field: 'id',
        sort: 'asc',
        width: 200
      },
      {
        label: 'Order By',
        field: 'orderBy',
        sort: 'asc',
        width: 200
      },
      {
        label: 'Date',
        field: 'date',
        sort: 'asc',
        width: 200
      },

      {
        label: 'Payment Stat',
        field: 'paymentStat',
        sort: 'asc',
        width: 'auto'
      },
      {
        label: 'Action',
        field: 'action',
        sort: 'asc',
        width: 'auto'
      },
    
    ],
    rows: 
   
    [...Corder.map((row,i) => (
      
     {
        id: `${row.id}`,
        orderBy: `${row.orderBy}`,
        date: `${row.date}`,
      
        paymentStat: `${row.paymentStat}`,
        action:
        <div style={{maxWidth:'310px',float:'right',marginLeft:'0px'}}><MDBBtn style={{float:'left'}} onClick={()=>toggle(row.orderDB)} color='yellow' size="sm"> Items</MDBBtn> <MDBBtn style={{float:'left'}} onClick={()=>approve(row.orderDB)} color='approve' size="sm"> Approve</MDBBtn><MDBBtn  onClick={()=>reject(row.orderDB)} color='reject' size="sm"> Reject</MDBBtn></div>,


     }
     
     ))
    ]

  };

  return (
    <Fragment>
   
    <MDBDataTable
      striped
      bordered
      small
      responsive
      data={data}
    />

 <MDBModal
    size='lg'
    isOpen={this.state.modal}
    toggle={()=>close()}
    centered
    className='singleModal'
  >
 <MDBModalHeader toggle={()=>close()} style={{backgroundColor:'#231F20',color:'white'}}><span style={{color:'#FAE933',marginLeft:'8px'}}>Order Items</span></MDBModalHeader>
    <MDBModalBody>
      <div className='imagecom'>
      <h6>Order#: {order.orderID}</h6>
        <MDBTable hover className='tablescroll'>
          <MDBTableHead color='blue-grey lighten-4'>
            <tr>
            
              <th>Artist Name </th>
              <th>Artwork Name</th>
              <th>Artwork Size</th>
              <th>Payment Amount</th>
              <th>Artwork Material</th>
              <th>Framing Options</th>
              <th>Price Per Piece</th>
              <th>Quantity</th>
              <th>Action</th>
            </tr>
          </MDBTableHead>

          <MDBTableBody>

          {this.state.items.map((data) => (
              <Fragment>
  
              <tr>
           
                <td>{data.artistName}</td>
                <td>{data.artworkName}</td>
                <td>{data.artworkSize}</td>
                <td>{data.artworkPaymentAmount}</td>
                <td>{data.artworkMaterial}</td>
                <td>{data.artworkFramingOptions}</td>
                <td>{data.artworkPrice}</td>
                <td>{data.artworkQuantity}</td>
                <td className='actions'>
                  <DownloadImage data={data.artworkImg} />
                </td>
              </tr>
              </Fragment>
            ))}
          </MDBTableBody>
        </MDBTable>
      </div>
    </MDBModalBody>
  </MDBModal>

<MDBContainer>
  <MDBModal isOpen={this.state.modalConfApprove}  size="small" centered>
      <MDBModalHeader  style={{backgroundColor:'#231F20'}} ><span style={{color:'#FAE933',marginLeft:'8px'}}>Confirmation</span></MDBModalHeader>
        <MDBModalBody > 
         Are you sure you to approve this order?
        </MDBModalBody>
        <MDBModalFooter>
          <MDBBtn color="secondary" size="sm" onClick={()=>close()}>Cancel</MDBBtn>
          <MDBBtn color="primary" size="sm" onClick={()=>confirmApprove()}>Confirm</MDBBtn>
        </MDBModalFooter>
      </MDBModal>
      </MDBContainer>

      <MDBModal isOpen={this.state.modalConfReject}  size="sm" centered>
      <MDBModalHeader  style={{backgroundColor:'#231F20',textAlign:'center'}}><span style={{color:'#FAE933',marginLeft:'8px'}}>Confirmation</span></MDBModalHeader>
        <MDBModalBody>
          Are you sure you to reject this order?
        </MDBModalBody>
        <MDBModalFooter>
          <MDBBtn color="secondary" size="sm" onClick={()=>close()}>Cancel</MDBBtn>
          <MDBBtn color="primary" size="sm" onClick={()=>confirmReject()}>Confirm</MDBBtn>
        </MDBModalFooter>
      </MDBModal>

  </Fragment>
  );
}

return (
  <PendingOrderTable/>
)
}
}



export default inject('startingStore')(observer(PendingOrder))