
import { Grid, Typography } from '@material-ui/core';
import { message } from 'antd';
import {
  MDBBtn, MDBDataTable, MDBModal,
  MDBModalBody,
  MDBModalFooter, MDBModalHeader
} from 'mdbreact';
import { inject, observer } from 'mobx-react';
import React, { Component, Fragment } from 'react';
import { MDBIcon, MDBTabPane, MDBTabContent, MDBNav, MDBNavItem, MDBNavLink } from "mdbreact";
 class PendingArtwork extends Component {
  state = {
    modal: false,
    items:[],
    activeItem:'1'
   
  };
  toggleTab = tab => e => {
    if (this.state.activeItem !== tab) {
      this.setState({
        activeItem: tab
      });
    }
  };

   componentDidMount() {   
    let {
      startingStore: { getArtists, getStyles, getCategories,getAllArtworks},
    } = this.props;
    
    getAllArtworks()
    getArtists();
    getStyles();
    getCategories();

  }
  

  render() {
    let { startingStore: {listOfArtworks, editArtwork, artwork}} = this.props;

 
    function createData(artworkDB,id,title,artist, style,  price,action) {
      return { artworkDB,id,title,artist, style,  price,action };
    }

let pArtworks = listOfArtworks.filter((art) => {
            if ( art.artworkStatus === 'Active') {
              return art;
            }
          }).map(artworks =>{
            return(createData(
           artworks,artworks.artworkID,artworks.artName
           ,artworks.artistName,artworks.artStyle
           ,artworks.artPrice
            ))
          })


        let  moreInfo = (itm) => {
          artwork.setProperty('artStyle',itm.artStyle)
        
          artwork.setProperty('artTheme',itm.artTheme)
          artwork.setProperty('artSize',itm.artSize)
          artwork.setProperty('artCategory',itm.artCategory)
          artwork.setProperty('artworkID',itm.artworkID)
          artwork.setProperty('artName',itm.artName)
          artwork.setProperty('artDescription',itm.artDescription)
          artwork.setProperty('artPrice',itm.artPrice)
          artwork.setProperty('artistName',itm.artistName)
          artwork.setProperty('artworkDateCreated',itm.artworkDateCreated)
          artwork.setProperty('artDimension',itm.artDimension)
          artwork.setProperty('artType',itm.artType)
          artwork.setProperty('dateAdded',itm.dateAdded)
          artwork.setProperty('artworkImg',itm.artworkImg)

          artwork.setProperty('artHeight',itm.artHeight)
          artwork.setProperty('artWidth',itm.artWidth)
          artwork.setProperty('artFormat',itm.artFormat)
          artwork.setProperty('artDPI',itm.artDPI)
          this.setState({
            modal: !this.state.modal
          });
       
          };

          let  close = () => {
            this.setState({
              modal: false
            });
            };

          let archive =(data)=>{
            artwork.setProperty('_id', data._id);
            artwork.setProperty('artworkStatus','Archived');
  
            editArtwork();

            const success = () => {
              message
                .loading('', 1)
                .then(() => message.success('Arwork Archived', 3));
            };

            setTimeout(() =>{
              success()
            },1000)
          }
        

const PendingArtworkTable = () => {
  const data = {
    columns: [
      {
        label: 'No.',
        field: 'no',
        sort: 'asc',
        width: 'auto'
      },
      {
        label: 'Title',
        field: 'title',
        sort: 'asc',
        width: 'auto'
      },
      {
        label: 'Artist',
        field: 'artist',
        sort: 'asc',
        width: 'auto'
      },

      {
        label: 'Style',
        field: 'style',
        sort: 'asc',
        width: 'auto'
      },
      {
        label: 'Price',
        field: 'price',
        sort: 'asc',
        width: 'auto'
      },
      {
        label: 'Action',
        field: 'action',
        sort: 'asc',
        width: 'auto'
      },
    
    ],
    rows: 
   
    [...pArtworks.map((row,i) => {
      let no = i+1;
      return(

     {
        no: `${no}`,
        title: `${row.title}`,
        artist: `${row.artist}`,
      
        style: `${row.style}`,
        price: `${row.price}`,
        action:<div><MDBBtn  onClick={()=>moreInfo(row.artworkDB)} color='approve' size='sm'> More Info</MDBBtn>
        <MDBBtn  onClick={()=>archive(row.artworkDB)} color='reject' size='sm'> Archive</MDBBtn></div>,
      

     }
     
     ) })
    ]

  };

  return (
    <Fragment>
    <MDBDataTable
      striped
      bordered
      small
      data={data}
    />


  
<MDBModal isOpen={this.state.modal}  centered>
  <MDBModalHeader toggle={()=>close()} style={{backgroundColor:'#231F20'}}><span style={{color:'#FAE933',marginLeft:'8px'}}>Artwork Information</span></MDBModalHeader>
          <MDBModalBody>
            <Grid container direction='row' >
            <Grid item xs={6}>
            <span className='arttitle'>
{artwork.artName}, {artwork.artworkDateCreated}
      </span>
       </Grid>
       <Grid item xs={6}>
            <Typography variant='subtitle2'  style={{float:'right'}}>
 Artwork ID: {artwork.artworkID}
      </Typography>
       </Grid>
       <Grid item xs={4}  style={{border:'1px solid grey'}}>
       <div className='artImg'  >
                    <img
                
                      src={artwork.artworkImg} alt='artwork' /></div>
</Grid>
<Grid item xs={8}  >
<div style={{paddingLeft:'10px'}}>
        <MDBNav className="nav-tabs ">
          <MDBNavItem>
            <MDBNavLink link to="#" active={this.state.activeItem === "1"} onClick={this.toggleTab("1")} role="tab" >
              Summary
            </MDBNavLink>
          </MDBNavItem>
          <MDBNavItem>
            <MDBNavLink link to="#" active={this.state.activeItem === "2"} onClick={this.toggleTab("2")} role="tab" >
             Properties
            </MDBNavLink>
          </MDBNavItem>
          <MDBNavItem>
            <MDBNavLink link to="#" active={this.state.activeItem === "3"} onClick={this.toggleTab("3")} role="tab" >
              Details
            </MDBNavLink>
          </MDBNavItem>
        </MDBNav>
        <MDBTabContent activeItem={this.state.activeItem} >
          <MDBTabPane tabId="1" role="tabpanel">
            <p className="mt-2">
            <Typography variant='body2'><b>Artist:</b> {artwork.artistName}</Typography>
            <Typography variant='body2'><b>Theme :</b> {artwork.artTheme}</Typography>
     <Typography variant='body2'><b>Style : </b>{artwork.artStyle} </Typography>
     <Typography variant='body2'><b>Type : </b>{artwork.artType}</Typography>
    <Typography variant='body2'><b>Pinting Size : </b>{artwork.artSize}</Typography>
    <Typography variant='body2'><b>Price : </b>&#8369;{`${Number(parseFloat(artwork.artPrice)).toLocaleString('en')}.00`}</Typography>
    <Typography variant='body2'><b>Uploaded : </b>{artwork.dateAdded}</Typography>
              
            </p>
          </MDBTabPane>
          <MDBTabPane tabId="2" role="tabpanel">
            <p className="mt-2">
            <Typography variant='body2'><b>Dimension : </b>{artwork.artDimension}</Typography>
            <Typography variant='body2'><b>Format : </b>{artwork.artFormat}</Typography>
    <Typography variant='body2'><b>Height : </b>{artwork.artHeight}</Typography>
     <Typography variant='body2'><b>Width : </b>{artwork.artWidth} </Typography>
    <Typography variant='body2'><b>DPI : </b> { artwork.artDPI >=72 ? (<span style={{color:'green'}}>{artwork.artDPI} <MDBIcon far icon="check-circle" style={{color:'green'}}/></span>):(<span style={{color:'red'}}>{artwork.artDPI} <MDBIcon far icon="times-circle" style={{color:'red'}}/></span>)} </Typography>
    
            </p>
       
          </MDBTabPane>
          <MDBTabPane tabId="3" role="tabpanel">
            <p className="mt-2">
            {artwork.artDescription}
            </p>
          </MDBTabPane>
        </MDBTabContent>


                    </div>
                    </Grid>
                    </Grid>
          </MDBModalBody>
          <MDBModalFooter>
            <MDBBtn color="secondary" onClick={()=>close()} size='sm'>Close</MDBBtn>
         
          </MDBModalFooter>
        </MDBModal>
  </Fragment>
  );
}

return (
  <PendingArtworkTable/>
)
}
}



export default inject('startingStore')(observer(PendingArtwork))

